




























































import Component from 'vue-class-component';
import Vue from 'vue';
import { ElForm } from 'element-ui/types/form';

interface SelOption {
    value?: any;
    label?: string;
    children?: SelOption[];
}

@Component
export default class Submissio extends Vue {
    public form: any = {};

    public base: SelOption[] = [];
    public tags: SelOption[] = [];
    public protocols: SelOption[] = [];

    public created() {
        this.init();
        this.getData();
    }

    public onSubmit() {
        const form = <ElForm>this.$refs.form;
        form.validate((valid: boolean) => {
            if (valid) {
                const now = Date.now();

                const data: IJson = {
                    ip: this.form.base[1],
                    port: this.form.port,
                    // tagNo: this.form.tagNo,
                    protocol: this.form.protocol
                    // + this.form.tagNo.padStart(8, '0')
                };

                this.$http
                    .post('/api/protocol/sendProtocol', data)
                    .then((res: ResponseData) => {
                        this.$message.success(res.message);
                        this.reset();
                    })
                    .catch(console.log);
            }
        });
    }
    public reset() {
        (<ElForm>this.$refs.form).resetFields();
        this.init();
    }

    private init() {
        this.form = {
            base: [],
            port: 50000,
            protocol: '',
            tagNo: ''
        };
    }

    private getData() {
        // 基站
        this.$http
            .get('/api/base/getall', {
                pageSize: 10000000,
                currentPage: 1
            })
            .then(this.toTree.bind(this))
            .catch(console.error);
        // 标签
        this.$http
            .get('/api/tag/getall', {
                pageSize: 10000000,
                currentPage: 1
            })
            .then((res: ResponseData) =>
                res.pagedData.datas.map((v: any) => ({
                    value: v.tagNo,
                    label: `${v.name}: ${v.tagNo}`
                }))
            )
            .then((tags: SelOption[]) => (this.tags = tags))
            .catch(console.error);
        // 协议
        this.$http
            .get('/api/protocol/getall', {
                pageSize: 10000000,
                currentPage: 1
            })
            .then((res: ResponseData) => {
                this.protocols = res.pagedData.datas.map((v: any) => ({
                    value: v.content,
                    label: v.name
                }));
            })
            .catch(console.error);
    }

    private toTree(base: ResponseData) {
        this.base.length = 0;

        const group: { [key: string]: SelOption[] } = {};
        base.pagedData.datas.forEach((v: any) => {
            (group[v.groupCode] || (group[v.groupCode] = [])).push({
                value: v.ip,
                label: `${v.name}: ${v.baseNo}; ${v.main ? '主' : '从'}基站`
            });
        });

        for (const [k, v] of Object.entries(group)) {
            this.base.push({
                label: `分组${k}`,
                children: v
            });
        }
    }
}
